body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to left, rgb(205, 92, 92), rgb(255, 215, 0));;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
    font-family: 'Bitline Regular';
    font-style: normal;
    font-weight: normal;
    src: local('Bitline Regular'), url(/dwthefirst/robofriends.git/static/media/BitlineRegular-axoMx.2bb8fde9.woff) format('woff');
    }

h1 {
    font-family: 'Bitline Regular';
    font-weight: 500;
    color: rgba(40, 5, 0, 0.706);
}
